.HomeLink{
  float:right;
  padding-right:20px;
}

div.h1{
  padding-top:5px;
  padding-bottom:5px;
  font-family:serif;
  font-style:normal;
  font-size:30px;
  text-align:center;
  color:#333;
}

/* Small mobile*/
@media only screen and (max-width: 400px) {
  div.h1{
      font-size:25px;
  }
}

.Browse{
  width: 100%;
  overflow-x: scroll;
  position: relative;
}

.displayTable{
  border:solid green 2px;
  width: 100%;
}

.displayTable td, th{
  border:solid yellow 2px;
  text-align:center;
}

.probParent{
   width:800px;
   max-width:100%;
   resize:none;
   height:250px; /* or whatever width you want. */
   max-height:250px; /* or whatever width you want. */
   display: inline;
   overflow: scroll;
}

.EditButton {
   border:2px solid #ddd;
   background-color: #555;
   color:white;
   padding:5px 20px;
   width: 5%;
   min-width: 150px;
   -webkit-transition: all 0.1s ease-in-out;
   -moz-transition: all 0.1s ease-in-out;
   -ms-transition: all 0.1s ease-in-out;
   -o-transition: all 0.1s ease-in-out;
   transition: all 0.1s ease-in-out;
/*font-size:17px;*/
  float:right;
}

.LeftButton{
  border:2px solid #ddd;
  background-color: #555;
  color:white;
  padding:5px 20px;
  width: 5%;
  min-width: 150px;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -ms-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.Question{
font-style:italic;
padding-left:2em;
font-family:georgina,garamond,serif;
}

.Answer{
color:gray;
/*visibility:hidden;*/
display:none;
}

.showAnswer{
 background-color: transparent;
  border:2px solid #ddd;
  padding:5px 20px;
  width: 5%;
  min-width: 150px;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -ms-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}
