.simple-dialog{
  position: fixed;
  background: white;
  color:gray;
  width: 80%;
  height: 90%;
  top:5%;
  left: 40%;
  padding:10px;
  z-index:9001;
  border-color: dimgray;
  border-style:double;
  transform: translateX(-40%) scale(1,1);
  backface-visibility: hidden;
  -webkit-transform: translateX(-40%) scale(1,1);
  -webkit-backface-visibility: hidden;
  border-radius: 2%;
  overflow-y: scroll;
  padding-bottom: 30px;
  background-color: #661155;
}

@media only screen and (max-width: 500px) {
  .simple-dialog{
    width: 90%;
  }
}

