.loading{
  -webkit-animation: sk-scalout 1.0s infinite eas-in-out;
  -moz-animation: sk-scalout 1.0s infinite eas-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
  background-color: black;
  border-radius: 100%;
  height: 6em;
  width: 6em;
}

.loadingContainer{
  position: absolute;
  top: 20em;
  left:35em;
  opacity: 0.3;
  filter: alpha(opacity=30);/*FOR IE8 and earlier*/
  align-items: center;
/*  background-color: azure;*/
  height: 10vh;
  justify-content: center;
  width: 20vw;
}


@keyframes sk-scaleout {

  0%{
    -moz-transform: scale(0);
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  100%{
        -moz-transform: scale(1.0);
    -webkit-transform: scale(1.0);
    opacity: 0;
    transform: scale(2.0);
  }
}
